body{
    background-color: pink;
}
.App {
    text-align: center;
    background-color: whitesmoke;
    width: 50rem;
    height: 80%;
    position: absolute;
    top: 10%;
    left: 20%;
    
  }
  
  .persons-list {
    flex-wrap: wrap;
    justify-content: center;
  }
  
  .person-card {
    margin: 10px;
    padding: 5px;
    border: 1px solid #ccc;
    border-radius: 10px;
    text-align: center;
    display: flex;
  }

  .button {
    background-color: pink;
    border-radius: 1%;
  }
  
  .person-card img {
    width: 100px;
    height: 100px;
    border-radius: 50%;
  }
  
  .person-card h2 {
    margin-top: 20px;
    margin-left: 20px;
  }
  
  .person-card p {
    margin-bottom: 0;
    margin-top: 50px;
    text-align: right;
  }